import React from "react";
import * as firebase from "firebase";
import "bootstrap/dist/css/bootstrap.min.css";
import "./Login.css";
import Loader from "./loading";
import bgSvg from "../assets/bg.png";
import fbSvg from "../assets/facebook.svg";
import instaSvg from "../assets/instagram.svg";
import gitSvg from "../assets/github.png";
import { InputGroup } from "react-bootstrap";

class Login extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      Email: "",
      Password: "",
      currenUser: null,
      loading: false,
      ErrorMsg: undefined,
    };
    this.EmailOnChange = this.EmailOnChange.bind(this);
    this.PasswordOnChange = this.PasswordOnChange.bind(this);
    this.SignIn = this.SignIn.bind(this);
    this.SignedUp = this.SignedUp.bind(this);
    this.errorHandle = this.errorHandle.bind(this);
  }
  errorHandle(msg) {
    if (msg.code == "auth/wrong-password") {
      this.setState({
        ErrorMsg: "Wrong Password!",
      });
    } else {
      this.setState({
        ErrorMsg: "Koi masla occured!",
      });
    }
  }
  SignIn(e) {
    e.preventDefault();
    this.setState({
      loading: true,
      errorHandle: undefined,
    });
    firebase
      .auth()
      .signInWithEmailAndPassword(this.state.Email, this.state.Password)
      .then((u) => {
        this.setState({
          currenUser: this.state.email,
        });
        console.log("Sucessfull!");
      })
      .catch((error) => {
        this.setState({
          loading: false,
        });
        this.errorHandle(error);
        console.log(error);
      });
  }
  EmailOnChange(e) {
    this.setState({
      Email: e.target.value,
    });
  }
  PasswordOnChange(e) {
    this.setState({
      Password: e.target.value,
    });
  }
  SignedUp() {
    this.props.signUpStatus(true);
    console.log("Sign UP!");
  }

  render() {
    return (
      <div className="login-outer">
        <div class="Login-main">
          <div>
            <img class="bg-" src={bgSvg} />
          </div>
          <div className="Login">
            <form className="loginForm">
              <h1>Sign In</h1>
              <div className="inputFields">
                <label>Email:</label>
                <input
                  type="text"
                  value={this.state.Email}
                  name="email"
                  placeholder="Email..."
                  onChange={this.EmailOnChange}
                />
              </div>
              <div className="inputFields">
                <label>Password:</label>
                <input
                  type="password"
                  name="password"
                  placeholder="Password..."
                  value={this.state.Password}
                  onChange={this.PasswordOnChange}
                />
              </div>

              <div className="btnAlign">
                <div className="errorAtLogin">
                  <p>
                    {this.state.ErrorMsg !== undefined
                      ? this.state.ErrorMsg
                      : " "}
                  </p>
                </div>
                <button onClick={this.SignIn} className="btn primary SignInBTN">
                  {this.state.loading ? (
                    <Loader version="Loading_small_black" />
                  ) : (
                    " "
                  )}
                  Sign In
                </button>
              </div>
              <div className="Align">
                <p>
                  By signing in you are agreeing to all the user terms and
                  services!
                </p>
                <p>
                  Don,t have an account?{" "}
                  <a onClick={this.SignedUp} id="SignUp">
                    {" "}
                    Sign up
                  </a>
                </p>
              </div>
            </form>
          </div>
        </div>
        <footer className="App-footer">
          <div className="footer-site-info">
            <h3>Ao na kadi haveli te khushbu la ke...</h3>
            <p>DEAD-MUNSHI V 1.1.2</p>
          </div>

          <div className="social-links">
            <a
              href="https://web.facebook.com/HmMunybb/"
              target="_blank"
              className="icon-wrapper bg-wrapper"
            >
              <img alt="facebook" className="icon" src={fbSvg} />
            </a>
            <a
              href="https://www.instagram.com/munyb.xxr/"
              target="_blank"
              className="icon-wrapper bg-wrapper"
            >
              <img alt="instagram" className="icon" src={instaSvg} />
            </a>
            <a
              href="https://github.com/HM-MUNEEB"
              target="_blank"
              className="icon-wrapper"
            >
              <img alt="GitHub" className="git-icon" src={gitSvg} />
            </a>
          </div>
        </footer>
      </div>
    );
  }
}

export default Login;
