import React, { useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "./posts.css";
import { Container, Row, Col } from "react-bootstrap";
import global_user_svg from "../assets/global_user.png";
import check_mark_svg from "../assets/check.svg";
import ReadMoreReact from "read-more-react";
import DialogboxPost from "./subcomponents/dialogboxPost";

function Posts(props) {
  let AllPosts = props.GPosts;
  const dialogbox = () => {
    return <DialogboxPost></DialogboxPost>;
  };
  let renderPost;
  if (AllPosts !== null && AllPosts !== undefined) {
    renderPost = AllPosts.map((post) => {
      return (
        <Col>
          <div className="GPOST" key={post.autherID + post.date}>
            <div className="post_header">
              <div className="post_user_info">
                <img className="global_user_svg" src={global_user_svg} />
                <h3>{post.outherUserName}</h3>
              </div>
              <div className="status">
                {post.status == "approved" ? (
                  <img className="check_mark_svg" src={check_mark_svg} />
                ) : (
                  "x"
                )}
              </div>
            </div>
            <div className="post_content">
              <h4>Arz kiya ha: </h4>
              <p className="content">
                <ReadMoreReact
                  text={post.content}
                  min={100}
                  ideal={100}
                  max={150}
                  readMoreText={<div className="seeMore">See more...</div>}
                />
              </p>
            </div>

            <p className="post_date">{post.date}</p>
          </div>
        </Col>
      );
    });
  }

  return (
    <center>
      <Container className="PostsContainer">
        <Row>{renderPost}</Row>
      </Container>
    </center>
  );
}

export default Posts;
