import React from "react";
import "./Gloading.css";
import LogoLoading from "../assets/DEAD-MUNSHI-LOADING.png";

function Gloading(props) {
  return (
    <div className="GLaoding" style={{ opacity: "100%" }}>
      <div className="spin-logo">
        <img src={LogoLoading} />
      </div>
    </div>
  );
}
export default Gloading;
