import React from "react";
import * as firebase from "firebase";
import Login from "./components/Login";
import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import Loader from "./components/loading";
import PostSection from "./components/postSection";
import SignUp from "./components/SignUp";
import logoutSvg from "./assets/logout.svg";
import postsSvg from "./assets/posts.svg";
import userSvg from "./assets/user.svg";
import Posts from "./components/posts";
import GLoading from "./components/GLoading";
import reloadSvg from "./assets/reload.svg";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      USER: null,
      CU: null,
      newPost: false,
      signUpStatus: false,
      userInfo: null,
      infoCheck: false,
      ALLPOSTS: null,
      GLoading: false,
    };
    this.signUpState = this.signUpState.bind(this);
    this.newPostClick = this.newPostClick.bind(this);
    this.changePostSectionState = this.changePostSectionState.bind(this);
    this.loadPosts = this.loadPosts.bind(this);
    this.logout = this.logout.bind(this);
    this.setGLoading = this.setGLoading.bind(this);
    this.loadPosts();
  }
  signUpState(signUpStatus) {
    this.setState({ signUpStatus });
  }
  getUserInfo() {
    const fire = firebase.firestore();
    const user = firebase.auth().currentUser;
    fire
      .collection("USERS_DATA")
      .doc(user.uid)
      .get()
      .then((doc) => {
        let UserObj;
        const dat = doc.data();
        UserObj = dat;
        if (UserObj !== undefined) {
          this.setState({
            userInfo: UserObj,
            infoCheck: true,
          });
        } else {
          console.log("ERROR 404, While reteriving the info obj");
        }
      })
      .catch((err) => {
        console.log(err);
      });
  }
  authListener() {
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.setState({
          USER: true,
          CU: user.email,
        });
        this.setGLoading();
        this.getUserInfo();
        this.loadPosts();
      } else {
        this.setState({ USER: null });
      }
    });
  }
  componentDidMount() {
    this.authListener();
  }

  logout(e) {
    e.preventDefault();
    firebase.auth().signOut();
    this.setState({
      USER: null,
      CU: null,
      newPost: false,
      signUpStatus: false,
      userInfo: null,
      infoCheck: false,
      ALLPOSTS: null,
    });
  }
  newPostClick(e) {
    this.state.newPost
      ? this.setState({ newPost: false })
      : this.setState({ newPost: true });
  }
  changePostSectionState() {
    this.setState({
      newPost: false,
    });
  }

  loadPosts() {
    console.log(this.state.userInfo);
    this.setState({
      ALLPOSTS: null,
    });
    const fire = firebase.firestore();
    const user = firebase.auth().currentUser;
    const POSTS = [];
    fire
      .collection("POSTS")
      .orderBy("date")
      .get()
      .then(function (querySnapshot) {
        querySnapshot.forEach(function (doc) {
          POSTS.push(doc.data());
        });
      })
      .catch((err) => {
        console.log(err);
      });
    setTimeout(() => {
      this.setState({
        ALLPOSTS: POSTS,
      });
    }, 1000);
  }
  setGLoading() {
    this.setState({
      GLoading: true,
    });
    setTimeout(() => {
      this.setState({
        GLoading: false,
      });
    }, 3000);
  }

  render() {
    let info;
    if (this.state.infoCheck) {
      info = this.state.userInfo.firstName;
    }
    return (
      <div
        className="App"
        style={
          this.state.USER && !this.state.signUpStatus
            ? { "background-color": "#393c43" }
            : { "background-color": "#393c43" }
        }
      >
        <div
          style={
            this.state.GLoading ? { overflow: "hidden" } : { overflow: "auto" }
          }
        >
          {this.state.GLoading ? (
            <div className="Global-Loading">
              <GLoading></GLoading>
            </div>
          ) : (
            ""
          )}

          <header
            className="header"
            style={
              this.state.USER && !this.state.signUpStatus
                ? { position: "fixed" }
                : { position: "relative" }
            }
          >
            <div className="logo-Enclosure">
              <div className="Logo"></div>
            </div>

            <div className="tagline-enclosure">
              <h3>Dead Munshi</h3>
              <p> A Facebook for jigars</p>
            </div>
            <div>
              {this.state.USER && !this.state.signUpStatus ? (
                <div className="right-side-contents">
                  {this.state.userInfo != null ? (
                    <div className="userName">
                      <img alt="user" src={userSvg} className="userSvg" />
                      <h3>{this.state.userInfo.userName}</h3>
                    </div>
                  ) : (
                    ""
                  )}

                  <div onClick={this.loadPosts}>
                    <img alt="Reload" src={reloadSvg} className="reloadSvg" />
                  </div>
                </div>
              ) : (
                ""
              )}
            </div>
          </header>

          {this.state.USER && !this.state.signUpStatus ? (
            <div className="app-main">
              <nav className="navbar">
                <ul className="navbar-nav">
                  {this.state.USER ? (
                    <li className="navitem">
                      <div className="UserPosts" onClick={this.newPostClick}>
                        <img className="newPostSvg" src={postsSvg} />
                      </div>
                      <div className="spaceSeperator"></div>
                    </li>
                  ) : (
                    " "
                  )}
                  {this.state.USER ? (
                    <li className="navitem ">
                      <div className="logout" onClick={this.logout}>
                        <img src={logoutSvg} className="logoutSvg" />
                      </div>
                    </li>
                  ) : (
                    ""
                  )}
                </ul>
              </nav>

              {this.state.newPost ? (
                <PostSection
                  className="postSection"
                  handleState={this.changePostSectionState}
                />
              ) : (
                ""
              )}

              {this.state.ALLPOSTS !== null &&
              this.state.ALLPOSTS !== undefined ? (
                <div
                  className="posts_align"
                  style={
                    this.state.newPost
                      ? { "margin-left": "28%" }
                      : { "margin-left": "0%" }
                  }
                >
                  <Posts
                    className="AllPosts"
                    GPosts={this.state.ALLPOSTS}
                  ></Posts>
                </div>
              ) : (
                <div className="Loader">
                  <center>
                    <Loader version="Main"></Loader>
                  </center>
                </div>
              )}
            </div>
          ) : !this.state.signUpStatus ? (
            <Login signUpStatus={this.signUpState}></Login>
          ) : (
            <SignUp signUpStatus={this.signUpState}></SignUp>
          )}
        </div>
      </div>
    );
  }
}

export default App;
