import React from "react";
import * as firebase from "firebase";
import Loader from "./loading";
import "bootstrap/dist/css/bootstrap.min.css";
import "./postSection.css";
import CurrentOutherPosts from "./subcomponents/currentOutherPosts";
import { findByTestId } from "@testing-library/react";
import { findRenderedComponentWithType } from "react-dom/test-utils";
import cancelSvg from "../assets/cancel.svg";

class PostSection extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      errorMsg: null,
      postUploadLoading: false,
      userPosts: "",
      post: {
        outherID: null,
        content: "",
        date: null,
        status: "approved",
        outherName: "",
        outherUserName: "",
        postUID: "",
        imgURL: "",
      },
    };
    this.loadUserPost();
    this.postOnChange = this.postOnChange.bind(this);
    this.postBtn = this.postBtn.bind(this);
  }

  postBtn(e) {
    e.preventDefault();
    this.setState({
      errorMsg: "",
      postUploadLoading: true,
      userPosts: "", //sets the user posts to null so that the loading happends and proper refreshes happens
    }); // updates the user post profile

    const fire = firebase.firestore();
    const user = firebase.auth().currentUser;
    // will go to read auto
    fire
      .collection("USERS_DATA")
      .doc(user.uid)
      .get()
      .then((doc) => {
        let userData;
        userData = doc.data();
        if (userData != null) {
          this.setState((prevState) => ({
            post: {
              // object that we want to update
              ...prevState.post, // keep all other key-value pairs
              outherName: userData.firstName, // update the value of specific key
              outherUserName: userData.userName,
            },
          }));
        } else {
          console.log("ERROR 404, recieving data from DB!");
        }
      });

    let date = new Date().toLocaleString();
    let userID = user.uid;

    this.setState((prevState) => ({
      post: {
        // object that we want to update
        ...prevState.post, // keep all other key-value pairs
        // update the value of specific key
        date: date,
        outherID: userID,
      },
    }));

    setTimeout(() => {
      if (this.state.post.content !== "") {
        let postUID = user.uid + "_" + this.state.post.date;
        postUID = postUID.replace(/\s/g, ""); //replaces string with ""
        postUID = postUID.replace(/,/g, ""); // replaces commas with ""
        postUID = postUID.replace(/\//g, "_"); // replaces forword slahes with ""
        this.setState((prevState) => ({
          post: {
            // object that we want to update
            ...prevState.post, // keep all other key-value pairs
            // update the value of specific key
            postUID: postUID,
          },
        }));
        fire.collection("POSTS").doc(postUID).set(this.state.post);
        fire
          .collection("USERS_DATA")
          .doc(user.uid)
          .collection("POSTS")
          .doc(postUID)
          .set(this.state.post);
        console.log("Posted!");
      } else {
        this.setState({
          errorMsg: "Post can not be empty!",
        });
      }
      this.setState({
        errorMsg: "",
        postUploadLoading: false,
      });
    }, 1000);
    setTimeout(() => {
      this.loadUserPost();
    }, 1000);
  }
  loadUserPost(e) {
    const fire = firebase.firestore();
    const user = firebase.auth().currentUser;
    const userData = [];
    //Get the current user posts from firebase
    fire
      .collection("USERS_DATA")
      .doc(user.uid)
      .collection("POSTS")
      .orderBy("date")
      .get()
      .then((docy) => {
        docy.forEach(function (doc) {
          // doc.data() is never undefined for query doc snapshots
          userData.push(doc.data());
        });
        userData.reverse(); // reverse's the array to decending order
      });
    setTimeout(() => {
      this.setState({
        userPosts: userData, // update the newly retrived posts
      });
    }, 1000);
  }
  postOnChange(e) {
    let i = e.target.value;

    this.setState((prevState) => ({
      post: {
        // object that we want to update
        ...prevState.post, // keep all other key-value pairs
        content: i, // update the value of specific key
      },
    }));
  }

  render() {
    return (
      <div className="postSection">
        <div className="HeaderPostsCategories">
          <p className="postsCategories">Create Post</p>
          <div className="cancel" onClick={this.props.handleState}>
            <img className="cancelIcon" src={cancelSvg} />
          </div>
        </div>
        <form className="createPost">
          <textarea
            value={this.state.post.content}
            onChange={this.postOnChange}
            class="form-control post-text-area"
          ></textarea>
          <button
            onClick={this.postBtn}
            className="btn btn-primary btn-sm post-btn"
          >
            {this.state.postUploadLoading ? (
              <Loader version="small-blackloading_small_black"></Loader>
            ) : (
              ""
            )}
            Post
          </button>
          <p>{this.state.errorMsg}</p>
        </form>
        {this.state.userPosts != "" ? (
          <CurrentOutherPosts Posts={this.state.userPosts}></CurrentOutherPosts>
        ) : (
          <center>
            <Loader className="loadingPosts" version="Main"></Loader>
          </center>
        )}
      </div>
    );
  }
}

export default PostSection;
