import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "./SignUp.css";
import Loader from "./loading";
import * as firebase from "firebase";
import { ThemeConsumer } from "react-bootstrap/esm/ThemeProvider";
import { Figure } from "react-bootstrap";
import bgSvg from "../assets/bg - signUp.png";
import fbSvg from "../assets/facebook.svg";
import instaSvg from "../assets/instagram.svg";
import gitSvg from "../assets/github.png";

class SignUp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      FNS: false,
      LNS: false,
      UNS: false,
      ES: false,
      PassS: false,
      CPassS: false,
      Confirmpassword: null,
      CPS: false,
      loading: false,
      Error: undefined,
      password: "",
      userInfo: {},
    };
    this.PasswordOnChange = this.PasswordOnChange.bind(this);
    this.EmailOnChange = this.EmailOnChange.bind(this);
    this.FirstNameOnChange = this.FirstNameOnChange.bind(this);
    this.LastNameOnChange = this.LastNameOnChange.bind(this);
    this.UserNameOnChange = this.UserNameOnChange.bind(this);
    this.ConfirmPasswordOnChange = this.ConfirmPasswordOnChange.bind(this);
    this.SignUp = this.SignUp.bind(this);
    this.loginClicked = this.loginClicked.bind(this);
  }
  errorHandle(msg) {
    if (msg == "404") {
      this.setState({
        Error: "Please fill out entire form!",
      });
    } else if (msg == "401") {
      this.setState({
        Error: "Please Correct Confirm Password!",
      });
    } else if (msg == "auth/email-already-in-use") {
      this.setState({
        Error: "This email address is already in use!",
      });
    } else {
      this.setState({
        Error: "Kuch na Kuch garbar ha ji! ðŸ˜‘",
      });
    }
  }
  SignUp(e) {
    e.preventDefault();
    this.setState({
      loading: true,
    });
    if (
      this.state.FNS &&
      this.state.PassS &&
      this.state.UNS &&
      this.state.LNS &&
      this.state.ES &&
      this.state.CPassS
    ) {
      if (this.state.password !== this.state.Confirmpassword) {
        this.setState({
          loading: false,
        });
        this.errorHandle("401");
      } else {
        let errorCode;
        let errorMessage;
        let email = this.state.userInfo.email;
        let pass = this.state.password;
        firebase
          .auth()
          .createUserWithEmailAndPassword(email, pass)
          .catch(function (error) {
            // Handle Errors here.
            errorCode = error.code;
            errorMessage = error.message;
          })
          .then((u) => {
            if (errorCode === undefined) {
              this.setState({
                error: "",
                email: "",
                password: "",
              });
              const fire = firebase.firestore();
              const user = firebase.auth().currentUser;
              const ref = fire.collection("USERS_DATA").doc(user.uid);
              ref.set({
                email: this.state.userInfo.email,
                userName: this.state.userInfo.userName,
                firstName: this.state.userInfo.firstName,
                lastName: this.state.userInfo.lastName,
              });
              this.SignedUp();
            }
            console.log("error code: ", errorCode);
            this.errorHandle(errorCode);
          });
      }
    } else {
      this.errorHandle("404");
      this.setState({
        loading: false,
      });
    }
  }
  SignedUp() {
    this.props.signUpStatus(false);
    console.log("Signed you UP! Now to the Main app");
  }
  EmailOnChange(e) {
    this.setState({
      ES: true,
    });
    let i = e.target.value;
    this.setState((prevState) => ({
      userInfo: {
        // object that we want to update
        ...prevState.userInfo, // keep all other key-value pairs
        email: i, // update the value of specific key
      },
    }));
  }
  PasswordOnChange(e) {
    this.setState({
      PassS: true,
      password: e.target.value,
    });
  }
  ConfirmPasswordOnChange(e) {
    this.setState({
      CPassS: true,
      Confirmpassword: e.target.value,
    });
  }
  FirstNameOnChange(e) {
    this.setState({
      FNS: true,
    });
    let i = e.target.value;
    this.setState((prevState) => ({
      userInfo: {
        // object that we want to update
        ...prevState.userInfo, // keep all other key-value pairs
        firstName: i, // update the value of specific key
      },
    }));
  }

  LastNameOnChange(e) {
    this.setState({
      LNS: true,
    });
    let i = e.target.value;
    this.setState((prevState) => ({
      userInfo: {
        // object that we want to update
        ...prevState.userInfo, // keep all other key-value pairs
        lastName: i, // update the value of specific key
      },
    }));
  }

  UserNameOnChange(e) {
    this.setState({
      UNS: true,
    });
    let i = e.target.value;
    this.setState((prevState) => ({
      userInfo: {
        // object that we want to update
        ...prevState.userInfo, // keep all other key-value pairs
        userName: i, // update the value of specific key
      },
    }));
  }
  loginClicked() {
    this.props.signUpStatus(false);
    console.log("Signed IN!");
  }

  render() {
    return (
      <div className="SignUp-outer">
        <div className="SignUp-main">
          <div>
            <img class="bg-" id="bg-margin" src={bgSvg} />
          </div>
          <div className="SignUP">
            <form className="SignUpForm">
              <div className="tagLine-align">
                <h5 onClick={this.loginClicked}> Login </h5>
                <h1>Sign Up</h1>
              </div>
              <div className="inputFields">
                <label>First Name</label>
                <input
                  value={this.state.userInfo.firstName}
                  onChange={this.FirstNameOnChange}
                  type="text"
                ></input>
              </div>
              <div className="inputFields">
                <label>last Name</label>
                <input
                  value={this.state.userInfo.lastName}
                  onChange={this.LastNameOnChange}
                  type="text"
                />
              </div>
              <div className="inputFields">
                <label>Username</label>
                <input
                  value={this.state.userInfo.userName}
                  onChange={this.UserNameOnChange}
                  type="text"
                />
              </div>
              <div className="inputFields">
                <label>Email</label>
                <input
                  value={this.state.userInfo.email}
                  type="email"
                  onChange={this.EmailOnChange}
                />
              </div>
              <div className="inputFields">
                <label>Password</label>
                <input
                  value={this.state.userInfo.passowrd}
                  type="password"
                  onChange={this.PasswordOnChange}
                />
              </div>
              <div className="inputFields">
                <label>Confirm Password</label>
                <input
                  value={this.state.userInfo.ConfirmPassword}
                  type="password"
                  onChange={this.ConfirmPasswordOnChange}
                ></input>
              </div>

              <div className="btnAlign">
                <p className="errorAtLogin">
                  {this.state.Error !== undefined ? this.state.Error : " "}
                </p>

                <button onClick={this.SignUp} className="btn primary SignUpBTN">
                  {this.state.loading ? (
                    <Loader version="Loading_small_black" />
                  ) : (
                    " "
                  )}
                  Sign Up
                </button>
              </div>
              <div className="Align">
                <p>
                  By signing up you are agreeing to all the user terms and
                  services!
                </p>
              </div>
            </form>
          </div>
        </div>
        <footer className="App-footer">
          <div className="footer-site-info">
            <h3>Ao na kadi haveli te khushbu la ke...</h3>
            <p>DEAD-MUNSHI V 1.1.2</p>
          </div>
          <div className="social-links">
            <a
              href="https://web.facebook.com/HmMunybb/"
              target="_blank"
              className="icon-wrapper bg-wrapper"
            >
              <img alt="facebook" className="icon" src={fbSvg} />
            </a>
            <a
              href="https://www.instagram.com/munyb.xxr/"
              target="_blank"
              className="icon-wrapper bg-wrapper"
            >
              <img alt="instagram" className="icon" src={instaSvg} />
            </a>
            <a
              href="https://github.com/HM-MUNEEB"
              target="_blank"
              className="icon-wrapper"
            >
              <img alt="GitHub" className="git-icon" src={gitSvg} />
            </a>
          </div>
        </footer>
      </div>
    );
  }
}

export default SignUp;
